@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;600;700;800&display=swap);
* {
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Rubik', sans-serif;
    height: 100vh;
}

.banner {
    background-image: url(/project-frontend-squad2/static/media/hero-bg.bb884368.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    height: 95vh;
    width: 100%;
}

div.title {
    padding: 36px 50px 0  50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

button.aboutButton {
    width: 13vw;
    height: 7vh;
    color: white;
    font-size: 14px;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 2.55px;
    background-color: #D842BD;
    border: 1px solid #D842BD;
    border-radius: 50px;
    margin-right: 3vw;
    cursor: pointer;
}
/* Alterações Giovanni  */
.linkRoute {
    text-decoration: none;
}
/* Alterações Giovanni  */
button.loginButton {
    width: 13vw;
    height: 7vh;
    color: white;
    font-size: 14px;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 2.55px;
    background-color: #3633F7;
    border: 1px solid #3633F7;
    border-radius: 50px;
    cursor: pointer;
}

img.logoSobre, img.logoLogin {
    padding-right: 4px;
    height: 0.7rem;
}

div.textBox {
    margin: 10% 45% 15% 10%;
}

h1 {
    color: white;
    font-family: 'Rubik Regular 400', sans-serif;
    font-size: 60px;
}

p.subtitle{
    color: white;
    font-size: 22px;
}

div.inputDiv {
    position: absolute;
    bottom: 2vh;
    left: 4vw;
    width: 90%;
    display: flex;
    justify-content: center;
    align-items: center;    
}

img.logoSearch {
    height: 20px;
    position: relative;
    left: 4vw;
    z-index: 2;
}
    

input.searchBar {
    width: 50%;
    height: 8vh;
    border: none;
    box-shadow: 8px 14px 30px #5538EE29;
    border-radius: 72px;
    z-index: 1;
    color: #8D9DA2;
    font-size: 22px;
    padding-left: 5%;
}

div.container {
    height: 600px;
}

.footer {
    position: relative;
    bottom: 0;
    background-color: #D842BD;
    color: white;
    width: 100%;
    height: 13vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.searchTitle {
    color: #3633F7;
    opacity: 1;
    font: normal normal bold 32px/35px Rubik;
    margin-top: 10vh;
    margin-bottom: 8vh;
    text-align: center;
}

.carouselImages {
    margin-left: 3rem;
    margin-right: 3rem;
}

.resultImage {
    border-radius: 21px;
    box-shadow: 0px 9px 27px #5538EE29;
}

.postBox {
    display: flex;
    height: 253px;
    width: 735px;
    border: 2px solid red;
    margin: 3rem;
    flex-wrap: wrap;
    border: 1px solid #5538ee24;
    background: #FFFFFF 0% 0% no-repeat padding-box;
    box-shadow: 0px 5px 10px #5538EE14;
    border-radius: 15px;
    opacity: 1;
}

.postImg {
    height: 82px;
    width: 82px;
    border-radius: 50px;
    margin-left: 5vh;
    margin-top: 2vh;
}

.postContainer {
    display: flex;
    justify-content: space-between;
    margin: 6rem;
}

.postUser {
    margin-left: 2vh;
    margin-top: 2vh;
    font-size: 32px;
    letter-spacing: 0px;
    color: #292929;
}

.postUsername {
    margin-left: 1vh;
    margin-top: 3vh;
    font-size: 20px;
    letter-spacing: 0px;
    color: #292929;
    opacity: 0.43;
}

.postText {
    width: 400px;
    position: absolute;
    margin-top: 8vh;
    margin-left: 18vh;    
    word-break: break-all;
}

.postLink {
    width: 185px;
    position: absolute;
    margin-left: 18vh;
    margin-top: 20vh;
    letter-spacing: 0px;
    color: #3634F7;
}

/* AJUSTES POST BOX - RESPONSIVE */
@media (max-width: 1500px) {
    .postText {
        width: 300px;
    }
}

@media (max-width: 1350px) {
    .postContainer {
        flex-direction: column;
    }
    .postBox {
        margin-left: 20vh;
    }
    .postText {
        width: 400px;
    }
    
}

/* MEDIA TABLET */
@media (max-width: 1050px) {

    .postContainer {
        flex-direction: column;
    }

    .postBox {
        margin-left: 0;
    }

    .postUsername{
        margin-left: 2vh;
        margin-top: 4vh;
    }

    .postText {
        margin-left: 24vh;
        margin-top: 11vh;
    }

    .postLink {
        margin-left: 24vh;
        margin-top: 26vh;
    }

    .banner {
        background-image: url(/project-frontend-squad2/static/media/hero-bg-mobile.752f34a4.jpg);
        background-size: cover;
        background-repeat: no-repeat;
        height: 90vh;
        width: 100%;
    }

    div.title {
        padding: 36px 16px 0  16px;
    }

    img.logo {
        width: 40vw;
    }

    button.aboutButton{
        width: 22vw;
        height: 5vh;
        margin-right: 13px;
        font-size: 16px;
    }

    button.loginButton{
        width: 22vw;
        height: 5vh;
        font-size: 16px;
    }

    img.logoSobre, img.logoLogin {
        height: 0.6rem;
    }

    div.textBox {
        margin: 10% 10% 15% 10%;
    }

    h1 {
        font-size: 40px;
    }

    p.subtitle{
        font-size: 20px;
    }

    div.inputDiv {
        position: relative;
        bottom: 2vh;
        left: 4vw;
    }

    img.logoSearch {
        height: 15px;
        top: 2.8vh;
        left: 5vw;
    }
        
    input.searchBar {
        width: 80%;
        height: 7vh;
        margin-top: 6vh;
        z-index: 1;
        font-size: 16px;
        padding-left: 8%;
    }

    .footer {
        height: 12vh;
        font-size: 12px;
    }

}

@media (max-width: 900px) {
    .postBox {
        display: flex;
        height: 253px;
        width: 100%;
        border: 2px solid red;
        margin: 0;
        margin-bottom: 4rem;
        flex-wrap: wrap;
        border: 1px solid #5538ee24;
        background: #FFFFFF 0% 0% no-repeat padding-box;
        box-shadow: 0px 5px 10px #5538EE14;
        border-radius: 15px;
        opacity: 1;
    }

    .postUser {
        font-size: 28px;
    }

    .postText {
        margin-left: 18vw;
        width: 50%;
    }

    .postContainer {
        margin-left: 0;
        /* align-items: center; */
        align-items: inherit;
        margin-left: 50px;
    }

    .postLink {
        margin-left: 15vh;
        margin-top: 26vh;
    }

    .postUsername {
        margin-left: 18vh;
        margin-top: 7vh;
        position: absolute;

        font-size: 16px;
        letter-spacing: 0px;
        color: #292929;
        opacity: 0.43;
    }
}

@media (max-width: 442px) {
    .postContainer {
        align-items: center;
        margin-left: 5vw;
        margin-right: 5vw;
    
    }

    img.postImg {
        margin-top: 2vh;
        margin-left: 4vw;
    }

    div.postUser {
        margin-top: 2vh;
    }

    div.postUsername {
        margin-top: 6vh;
        margin-left: 26vw;

    }

    div.postText {
        margin-top: 10vh;
        margin-left: 26vw;
        width: 60%;
    }

    div.postLink {
        margin-top: 26vh;
        margin-left: 26vw;
    }

    .postBox {
        align-items: flex-start;
        margin-left: 0;
    
    }

}
/* MEDIA MOBILE */
@media (max-width: 414px) {

    .banner {
        height: 70vh;
    }

    div.title {
        padding: 36px 16px 0  16px;
    }

    img.logo {
        width: 130px;
    }

    button.aboutButton{
        width: 22vw;
        height: 4vh;
        margin-right: 13px;
        font-size: 10px;
    }

    button.loginButton{
        width: 22vw;
        height: 4vh;
        font-size: 10px;
    }

    img.logoSobre, img.logoLogin {
        height: 0.5rem;
    }

    div.textBox {
        margin: 10% 10% 15% 10%;
    }

    h1 {
        font-size: 34px;
    }

    p.subtitle{
        font-size: 16px;
    }

    div.inputDiv {
        bottom: 1vh; 
    }

    img.logoSearch {
        height: 15px;
        top: 2.4vh;
        left: 7vw;
    }
        
    input.searchBar {
        width: 80%;
        height: 7vh;
        margin-top: 5vh;
        font-size: 16px;
        padding-left: 10%;
    }

    .footer {
        height: 10vh;
        font-size: 10px;
    }


    
}


* {
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Rubik', sans-serif;
}

.containerLogin {
        display: flex;
        flex-direction: column;
}

/* ---------- BACKGROUND----------  */
.backgroundImg {
    position: fixed;
    height: 100%;
    width: 100%;
}
/* ----------HEADER---------- */
.headerLogin {
    display: flex;
    position: relative;
    justify-content: space-between;
    

}


.itensHeader {
    margin: 2vw;
}


.butHome {
    background-color: #d842db;
    border-radius: 2vw;
    width: 10vw;
    height: 3vw;
}

.butHome span {
    text-decoration: none;
    color: white;
    font-size: 16px;
}

/* ----------LOGIN FORM----------  */
.loginBox {
    background-color: white;
    display: flex;
    flex-direction: column;
    position: relative;
    align-items: center;
    border-radius: 1vw;
    width: 25vw;
    margin-left: 37vw;
    margin-top: 7vw;
    padding: 4vw 3vw;
    margin-bottom: 10vw;
}


.titleBox {
    color: #3634f7;
}

.formLogin {
    display: flex;
    flex-direction: column;
}

.titleLogin {
    margin: 2vw;
}

.itensLogin {
    width: 22vw;
    margin: 2vw;
    box-shadow: 0px 1px 0px 0px;
    border: 0 none;
    outline: 1;
    height: 50px;
}

.itensLogin::-webkit-input-placeholder{
    color: black;
    margin: 2vw;
    font-size: 20px;
    padding: 5px;
}

.itensLogin:-ms-input-placeholder{
    color: black;
    margin: 2vw;
    font-size: 20px;
    padding: 5px;
}

.itensLogin::placeholder{
    color: black;
    margin: 2vw;
    font-size: 20px;
    padding: 5px;
}

.buttonLogin {
    color: white;
    background-color: #3634f7;
    border-radius: 2vw;
    width: 9vw;
    height: 3vw;
    font-size: 22px;
}


@media screen and (max-width:1100px) {
    .itensLogin {
        font-size: 20px;
        width: 30vw;
        margin: 2vw;
        box-shadow: 0px 1px 0px 0px;
        border: 0 none;
        outline: 0;
        height: 50px;
        margin: 4vw;
    }

    .itensHeader {
        height: 50px;
    }

    .titleBox {
        font-size: 4vw;
    }

    .buttonLogin {
        width: 20vw;
        height: 6vw;
        font-size: 30px;
        border-radius: 10vw;
        margin-bottom: -2vw;
    }

    .loginBox {
        margin-top: 20vw;
        border-radius: 2vw;
        width: 40vw;
        height: 40vw;
        margin-left: 28vw;
        padding: 4vw 3vw;
        margin-bottom: 10vw;
    }

    .butHome {
        width: 17vw;
        height: 5vw;
        border-radius: 10vw;
        
    }

    .butHome span {
        font-size: 30px;
    }

    .butHome img {
        height: 20px;
    }
}

button {
    border-style: hidden;
}

/* ---------- VERSAO MOB----------  */
@media screen and (max-width:850px) {

    .butHome {
        border-radius: 6vw;
        width: 25vw;
    }

    .loginBox {
        width: 80vw;
        height: 100vw;
        margin-left: 7vw;
        margin-top: 35vw;
        margin-bottom: 20vw;
    }

    .itensLogin {
        width: 65vw;
        margin: 10vw;
    }

    .titleBox {
        font-size: 10vw;
        margin-bottom: -10px;
    }

    .buttonLogin {
        border-radius: 13vw;
        width: 50vw;
        height: 15vw;
        font-size: 20px;
        margin-top: 4vw;
    }

    .butHome img {
        height: 15px;
    }

    .butHome span {
        font-size: 20px;
    }

    .itensHeader {
        height: 9vw;
    }

    .headerLogin {
        padding: 2vw;
    }

}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    font-family: "Rubik",sans-serif;
}

/*header*/
div.aboutWrapper{
    width: 100%;
    /* height: 100vh; */
}

div.aboutHeader{   
    background-image: url(/project-frontend-squad2/static/media/about-bg.27d21cb1.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    width: 100%;
    height: 26.375em;    
}

/*TOP COM HASHTAG E BOTOES*/
section.header{             
    width: 95vw;
    height: 80px;
    opacity: 1;
    padding: 0 16px;
   /* position: fixed; /*PARA DEIXAR O TOPO FIXO NA TELA*/
    margin: 0;    
}

div.headerContent{    
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
 }

 div.headerLogo{
    margin-left: 64px;
    width: 239px;
    height: 42px;
}

/*TAMANHO DO LOGO HASHTAG FINDER*/
img.logoWhite{ 
    width: 100%;
    height: 100%;
}

/*alinhamento botoes */
div.headerButtons{ 
    height: 100%;
    display: flex;
    align-items: center;
}

/* BOTOES */
div.headerButtons button{ 
    border-radius: 25px;
    border: none;
    font-size: 17px;
    font-weight: bold;
    letter-spacing: 1.5px;
    text-transform: uppercase;
    display: flex;
    justify-content: center;
    align-items: center;    
}

/* IMAGEM DO BOTAO */
div.headerButtons button img{ 
    width: 13px;
    height: 13px;
    margin-right: 8px;
}

button.aboutBtn{
    background: #D842BD;
    color: #fff;
    margin-right: 13px;
    cursor: pointer;
}

button.aboutBtnLogin{
    background: #3633f7;
    color: #fff;
    cursor: pointer;
}

 /*ESCRITA NO TOPO SOBRE PROJETO*/
h1.aboutProject{
    color: #ffff;
    font-size: 5.625em;
    margin-top: 8.25rem;
    padding-left: 8.75rem;
}

/*descrição do projeto projeto TEXTO LOREMIPSIM*/
div.aboutAside{
    width: 35.8125em;
    margin-right: 4vw;
}

/*O QUE É*/
div.aboutAside h3{
    color: #3634F7;
    margin-top: 0;
    font-size: 2.75em;
    margin-bottom: 1.5625rem;
}

div.aboutAside p{
    text-align: left;
    letter-spacing: 0px;
    color: #292929;
    font-size: 1.5625em;
}

div.aboutText{
    padding-top: 12.0625rem;
    text-align: left;
    /* width: 96.625rem; */
    align-items: flex-start;
    margin: 0 2vw;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

img.aboutSvg {
    width: 39.75rem;
    margin-left: 5vw;
}

/*quem somos nós*/
h1.aboutWe{
  
    padding-top: 12.0625rem;
    color: #3634F7;
    font-size: 2.75em;
    padding-bottom: 4.125rem;
    width: 96.625rem;
    margin: 0 auto;
}

div.wrapperPhoto{ /*CONTAINER DOS CARTOES*/    
    padding-bottom: 10.3125rem;
    width: 108rem;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
}

div.aboutTeam{
    margin-right: 1rem;
}

/*cards da equipe*/
div.aboutCard{
    width: 19rem;
    box-shadow: 0 5px 10px rgb(85 56 238 / 8%);
    border-radius: 10px;
    border: 1px solid rgba(85,56,238,.1411764705882353);
    height: 32.8125rem;
}

div.about-team-content{
    width: 19.25rem;
    display: flex;
    align-items: center;
    margin: 0 auto;
    flex-direction: column;
}

div.aboutPhoto{
    height: 12.6875rem;
    width: 12.6875rem;
    margin-bottom: 1.5625rem;
    margin-top: 2.5rem;
    border-radius: 100%;
    box-shadow: rgb(85 56 238 / 10%) 7px 23px 6px -13px;
    background-color: rgb(255, 255, 255)
}

div.aboutPhoto img{
    width: 100%;
    border: 1px solid #5538EE24;
    border-radius: 100%;
    height: 100%;
}

div.aboutCard h2{
    color: #292929;
    font-size: 2em;
    margin-top: 0;
    text-align:left;
    margin-bottom: 1.5625rem;
}

div.aboutCard p{
    color: #292929;
    margin-top: 0;
    font-size: 1.1875rem;
    height: 5.625em;
    text-align: center;
    margin-bottom: 1.75rem;
    letter-spacing: 0.23px;
}

div.aboutSocials{
    display: flex;
    width: 100%;
    justify-content: center;
}

div.aboutSocials img{
    font-size: 1.5625rem;   
    margin-right: 2.0625rem;
}

footer.footer {
    position: relative;
    bottom: 0;
    background-color: #D842BD;
    color: white;
    width: 100%;
    height: 13vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

