    }

    /* reponsividade descrição do projeto*/
    div.aboutText {
        padding: 0;
        margin: 0;
        width: 100%;
        display: flex;
        flex-direction: column;
    }

    div.aboutAside {
        margin: 4vh 0;
        padding: 0 4vw;
        width: 100%;
    }

    div.aboutAside h3 {
        font-size: 24px;
    }

    div.aboutAside p {
        width: 100%;
        font-size: 16px;
        padding-right: 6vw;
    }

    img.aboutSvg {
        width: 80vw;
        height: 50vh;
        margin: 2vh 0 0 0;
    }

    /*reponsividade seção quem somos nós*/
    h1.aboutWe {
        width: 100%;
        font-size: 24px;
        padding: 2vh 5vw;
    }

    /*reponsividade cards equipe*/

    /*EMBRULHO FOTOS*/
    div.wrapperPhoto {
        padding: 0;
        width: 100%;
        display: flex;
        flex-direction: column;
    }

    div.aboutTeam{
        margin: 0;
        width: 100%;
        display: flex;
        justify-content: center;
    }

    .aboutCard{
        width: 100%;
        margin-right: 0px;
        height: auto;
        height: initial;    
        margin-bottom: 2.875rem;
    }

    .aboutPhoto{
        height: 11.375rem;
        width: 11.375rem;
        margin-top: 2.25rem;
        margin-bottom: 1.375em;
    }

    .aboutCard h2{
        font-size: 28px;
        margin-bottom: 24px;
    }

    .aboutSocials{
        padding-bottom: 37px;
    }

    .aboutSocials img{
        font-size: 27px;
        margin-right: 30px;
    }
}


/* MEDIA MOBILE */
@media (max-width: 414px) {

    /* reponsividade header*/
    div.aboutHeader {
        height: 30vh;
    }

    /* box com logo e botões */
    section.header {
        padding: 0;
    }

    /* box com a logo */
    div.headerLogo {
        display: flex;
        align-items: left;
        margin: 8vw;
    }

    div.headerContent {
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
    }

    /* botão sobre */
    .header .headerContent .headerButtons .aboutBtn{    
        margin-right: 13px;
        font-size: 10px;
        width: 22vw;
        height: 30px;
    }
    
    /* botão login */
    .header .headerContent .headerButtons .aboutBtnLogin{
        font-size: 10px;
        width: 22vw;
        height: 30px;
    }

    /* título sobre o porjeto */
    h1.aboutProject {
        font-size: 30px;
    }

}


/* Alterações Giovanni  */
body {
