}

/* Alterações Giovanni  */

.backgroundImg {
    position: fixed;
    height: 100%;
    width: 100%;
    margin: 0;
    z-index: -1;
}

.containerSearch {
    display: flex;
    flex-direction: column;
}

.homeTitle {
    color: white;
    font-size: 35px;
    flex-grow: 1;
}

.header {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 36px 60px 0px 60px;
}

.homeButton {
    width: 170px;
    height: 49px;
    background-color: #D842BD;
    color: white;
    font-family: 'Rubik', bold;
    font-size: 17px;
    border-radius: 30px;
    border-style: hidden;
    margin-right: 35px;
    box-shadow: #5538ee29 0px 3px 6px 0px;
    cursor: pointer;
}

.homeButton img {
    margin-right: 5px;
}

.exitButton {
    width: 170px;
    height: 49px;
    background-color: #3633F7;
    color: white;
    font-family: 'Rubik', bold;
    font-size: 17px;
    border-radius: 30px;
    border-style: hidden;
    box-shadow: #5538ee29 0px 3px 6px 0px;
    cursor: pointer;
}

.exitButton img {
    margin-right: 5px;
}

/* Lista de hashtags*/

.homeTitle {
    font-size: 19px;
}

.container {
    margin: 40px 183px 85px 184px;
}

.container h1 {
    color: white;
    font-size: 40px;
}

table {
    border-collapse: collapse;
    width: 100%;
    border-radius: 1em;
    overflow: hidden;
}


thead th {
    background-color: #F1F1F1;
    color: #3633F7;
    text-align: center;
    padding: 56px 0px 22px 0px;
    ;
    font-size: 28px;
}

thead th:nth-child(1) {
    text-align: left;
    padding-left: 62px;
}

tbody {
    background-color: white;
    font-size: 24px;

}

tbody td {
    padding: 21px 58px 21px 58px;
    border-bottom: 3px solid #F1F1F1;
}

tbody td:nth-child(n+2) {
    color: #D842BD;
}

tbody td:nth-child(1) {
    width: 80%;
}

